/* Arrows */
.slick-prev,
.slick-next {
  top: 50%;
  position: absolute;
  display: block;
  outline: none;
  border: none;
  padding: 0;
  width: 20px;
  height: 20px;
  font-size: 0;
  line-height: 0px;
  color: transparent;
  background: transparent;
  transform: translate(0, -50%);
  transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer; }
  .slick-prev::before,
  .slick-next::before {
    font-family: "slick";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 20px;
    line-height: 1;
    color: white;
    opacity: 0.75; }
  .slick-prev:hover, .slick-prev:focus,
  .slick-next:hover,
  .slick-next:focus {
    outline: none;
    color: transparent;
    background: transparent; }
    .slick-prev:hover::before, .slick-prev:focus::before,
    .slick-next:hover::before,
    .slick-next:focus::before {
      opacity: 1; }
  .slick-prev.slick-disabled::before,
  .slick-next.slick-disabled::before {
    opacity: 0.25; }

.slick-prev {
  left: -25px; }
  .slick-prev::before {
    content: ""; }
    [dir="rtl"] .slick-prev::before {
      content: ""; }
  [dir="rtl"] .slick-prev {
    left: auto;
    right: -25px; }

.slick-next {
  right: -25px; }
  .slick-next::before {
    content: ""; }
    [dir="rtl"] .slick-next::before {
      content: ""; }
  [dir="rtl"] .slick-next {
    left: -25px;
    right: auto; }

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px; }

.slick-dots {
  bottom: -25px;
  position: absolute;
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  text-align: center; }
  .slick-dots li {
    position: relative;
    display: inline-block;
    margin: 0 5px;
    padding: 0;
    width: 20px;
    height: 20px;
    cursor: pointer; }
    .slick-dots li button {
      display: block;
      outline: none;
      border: 0;
      padding: 5px;
      width: 20px;
      height: 20px;
      font-size: 0;
      line-height: 0px;
      color: transparent;
      background: transparent;
      cursor: pointer; }
      .slick-dots li button::before {
        content: "";
        left: 0;
        top: 0;
        position: absolute;
        width: 20px;
        height: 20px;
        font-family: "slick";
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-size: 6px;
        line-height: 20px;
        text-align: center;
        color: black;
        opacity: 0.25; }
      .slick-dots li button:hover, .slick-dots li button:focus {
        outline: none; }
        .slick-dots li button:hover::before, .slick-dots li button:focus::before {
          opacity: 1; }
    .slick-dots li.slick-active button::before {
      color: black;
      opacity: 0.75; }

